import React, { useRef, useState, useEffect } from 'react';
import PlaceList from '../../../places/components/PlaceList';

import Button from './Button';

import './ImageUpload.css';

const ImageUpload = (props) => {

  const [file, setFile] = useState();
  const [previewUrl, setPreviewUrl] = useState();
  const [isValid, setIsValid] = useState(false);

  const filePickerRef = useRef();

  useEffect(() => {
    // if don't have file (or is undefined) return b/c can't generate a preview
    if (!file) {
      return;
    }
    //                     FileReader is API type method baked into browswer for JS usage
    const fileReader = new FileReader();
    // whenever FileReader loads a new file, or is done parsing a new file, have to call this function (it's not a callback or promise that's able to be used with .onload)
    // this executes once reading of file is done via .readAsDataURL below it
    fileReader.onload = () => {
      // just how API works that we get this result to pass
      setPreviewUrl(fileReader.result);
    };
    fileReader.readAsDataURL(file);
  }, [file]); // --> whenever file changes, useEffect should run

  const pickedHandler = (event) => {
    let pickedFile;
    // need to use this to define valid state b/c does not immediately update, if ths is not done, when validity is passed
    // to props.onInput() below, the validity will be older state
    let fileIsValid = isValid;
    // ensure there are files, there's exactly 1 file that can be selected
    if (event.target.files && event.target.files.length === 1) {
      pickedFile = event.target.files[0];
      setFile(pickedFile);
      setIsValid(true);
      fileIsValid = true;
    } else {
      setIsValid(false);
      fileIsValid = false;
    } // reminder onInput comes from Input.js file, and this maps to id, value, isValid in that file
    props.onInput(props.id, pickedFile, fileIsValid);
  };

  // open built in image picker (utilize input), which initially is invisible, but if could click it, would open up file picker
  // to simulate click, need access to input element
  const pickImageHandler = () => {
    // using ref (useRef) have ability to use click method of input el (utilize input element without seeing it)
    filePickerRef.current.click();
  };

  return (
    <div className="form-control">
      {/* note if attribute type="file" is set, accept becomes usable as another attribute supported by all browsers */}
      <input
        id={props.id}
        ref={filePickerRef}
        style={{ display: 'none' }}
        type="file"
        accept=".jpg, .png, .jpeg, .mp3"
        onChange={pickedHandler}
      />
      <div className={`image-upload ${props.center && 'center'}`}>
        <div className="image-upload__preview">
          {previewUrl && <img src={previewUrl} alt="Preview" />}
          {!previewUrl && <p>Please pick a valid image.</p>}
        </div>
        <Button type="button" onClick={pickImageHandler}>
          PICK IMAGE
        </Button>
      </div>
      {!isValid && <p>{props.errorText}</p>}
    </div>
  );
};

export default ImageUpload;
