import { useCallback, useReducer } from 'react';

const formReducer = (state, action) => {
  switch (action.type) {
    case 'INPUT_CHANGE':
      // helper variable
      let formIsValid = true;

      //loop through inputs (state arg passed via useReducer function where inputs are instnatiated)
      //
      for (const inputId in state.inputs) {
				// if this property is undefined, go to next iteration of for-loop
				// implemented to handle auth where 'name' input exists if signing up, but not if logging in
				if (!state.inputs[inputId]) {
					continue;
				}

        // if inputId (i.e. title or description) equals actions.id
        // is inputId passed via state the same that's updated with current action passed
        if (inputId === action.inputId) {
          // take info from dispatched action on whether is valid
          //            |                   |
          // ensures    v previous value +  v  current value  are combined
          // which in turns makes sure if anything is inValid over all form validity becomes false

          formIsValid = formIsValid && action.isValid;
        } else {
          // if looking at input in (form) state that's NOT currently being updated via action
          // take stored value of validity for this input b/c is input not currently being updated via action
          formIsValid = formIsValid && state.inputs[inputId].isValid;
        }
      }
      return {
        ...state,
        inputs: {
          // current input state
          ...state.inputs,
          // over ride current input with
          //dynamic assignment v  to update one of the input fields that's set via dispatch function
          [action.inputId]: { value: action.value, isValid: action.isValid },
          // ex:      title: {value: action.value, isValid: action.isValid}
          // desciption will be left alone
        },
        // set overall form vallidity
        isValid: formIsValid,
      };
    case 'SET_DATA':
      return {
        inputs: action.inputs,
        isValid: action.formIsValid,
      };
    default:
      return state;
  }
};

export const useForm = (initialInputs, initialFormValidity) => {
  const [formState, dispatch] = useReducer(formReducer, {
    // set initial state of tite & descriptoin inputs
    inputs: initialInputs,
    isValid: initialFormValidity,
  });

  /*
	NOTE: useCallBack used to prevent infinite loop...
	allows InputHandler functions to be stored behind the hood in React 
	as opposed to having a new function InputHandler object function created 
	every time this component renders
	*/
  const inputHandler = useCallback((id, value, isValid) => {
    dispatch({
      type: 'INPUT_CHANGE',
      value: value,
      isValid: isValid,
      inputId: id,
    });
  }, []);
  //  ^ dispatch could techinically be dependency passed here,
  // but React  ensures that disptach from useReducer never changes, so can be ommitted

  const setFormData = useCallback((inputData, formValidity) => {
    dispatch({
      type: 'SET_DATA',
      inputs: inputData,
      formIsValid: formValidity,
    });
  }, []);

  return [formState, inputHandler, setFormData];
};
