import { useState, useCallback, useRef, useEffect } from 'react';

export const useHttpClient = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState();

  // useRef built in React hook, turns data into reference that will not change
  // or for this usage, will not be reinitialized when function runs again (function runs again when component rerenders)
  // makes it so data is stored across rerender cycles
  const activeHttpRequests = useRef([]);

  // wrap in useCallback to prevent function from being recreated causing infinite loop
  // ex, us how sendRequest is used in Users.js file
  // when called in that file, is techincally considered a new object function when sendRequest is called, a
  // and therefore, this would repeatedly run and rerender in a loop
  const sendRequest = useCallback(
    async (url, method = 'GET', body = null, headers = {}) => {
      setIsLoading(true);
      // function supported in modern browswers
      const httpAbortCtrl = new AbortController();
      // useRef always wraps data in object that has current method
      // current holds array that doesn't change between rerender cycles
      activeHttpRequests.current.push(httpAbortCtrl);

      try {
        const response = await fetch(url, {
          method, // method: method
          body,
          headers,
          // links abort controller to request
          // abort controller will cancel the request
          signal: httpAbortCtrl.signal,
        });

        const responseData = await response.json();
        //         ^                ^
        // once have response, know that request completed

        // if request completes, need to remove abortController from array of abortControllers

        // filter out controller responsible for the specific request that completed
        activeHttpRequests.current = activeHttpRequests.current.filter(
          // remove requestController resonsible for request
          // i.e. keep every contoller, except for the one used for this request
          // prevents having old request controller which would then try to cancel request not on it's way anymore
          (reqCtrl) => reqCtrl !== httpAbortCtrl
        );

        // check if response returned from request is 400 or 500'ish
        if (!response.ok) {
          throw new Error(responseData.message);
        }
        setIsLoading(false);
        return responseData;
      } catch (err) {
        setError(err.message);
        console.log(err.message);
        setIsLoading(false);
        // throw error so component using hook knows that someting went wrong
        throw err;
      }
    },
    [] // useCallback has no dependencies (so will only run once)
  );

  const clearError = () => {
    setError(null);
  };

  // only runs once
  // whole purpose of abort controller is to make sure to never continue with a request that is on it's way out
  // if user switches away from component that triggered request (example navigatin to new page)
  useEffect(() => {
    // returned function in first arg function executes as a 'clean up' function
    // before the next time useEffect runs again OR when component that uses useEffect unmounts (i.e. component using the custom hook create with this file)

    return () => {
      activeHttpRequests.current.forEach((abortCtrl) => {
        console.log(abortCtrl.abort);
      });
    };
  }, []);

  return { isLoading, error, sendRequest, clearError };
};



